body {
  font-family: Authentic, sans-serif;
  font-weight: 60;
  font-size: 11pt;
}

.pagedjs_page_content {
  orphans: 2;
  widows: 2;
}

h2 {
  font-family: Louise-Regular, sans-serif;
  font-size: 64pt;
  text-align: center;
}

h3 {
  font-family: Authentic, sans-serif;
  font-weight: 150;
  text-align: center;
}
h3:first-child {
  margin-top: 0;
  padding-top: 0;
}

h5 {
  text-align: center;
  font-size: 11pt;
}

blockquote {
  margin-left: 1cm;
  margin-right: 1cm;
}
blockquote > p {
  margin: 0;
  padding: 0;
  text-indent: 0;
}

sup,
.footnote {
  font-family: Authentic, sans-serif;
  font-weight: 150;
  vertical-align: top;
  position: relative;
  top: -0.2em;
  font-size: 7pt;
}
.full-spread-image-caption,
.thumbcaption {
  font-family: Authentic, sans-serif;
  font-weight: 90;
  text-align: center;
}

.full-spread-image-caption {
  line-height: 8pt;
  text-align: left;
}

/* we have to disable the baseline grid margin/padding 
	that Plumber sets above for the first element on a page */
.chapter > p:first-child {
  margin-top: 0;
  padding-top: 0;
}

.pagedjs_margin {
  font-family: Authentic, sans-serif;
  font-weight: bold;
  font-size: 9pt;
}

.pagedjs_margin-bottom-left-corner-holder .pagedjs_margin-content,
.pagedjs_margin-bottom-right-corner-holder .pagedjs_margin-content {
  font-family: Louise-Regular, sans-serif;
}

/* 
Some style to help debugging PagedJs, layer order etc. 
*/
@media screen {
  .debug {
    /*
    For debugging the baseline grid append ?debug to the url 
    */
    /*
    	reset z-index on .pagedjs_area so right click works
    */
  }
  .debug .pagedjs_page_content {
    background-image: repeating-linear-gradient(to bottom, transparent 0px, transparent 11.25pt, rgba(255, 0, 0, 0.1) 11.25pt, rgba(255, 0, 0, 0.1) 12pt);
  }
  .debug .pagedjs_layout-title_page .pagedjs_area {
    z-index: unset;
  }
}
/* General styling */
body {
  color: var(--color-main);
  margin: 0;
  padding: 0;
  position: relative;
  overflow-x: hidden;
}

#webview {
  display: flex;
}

p {
  font-size: 14pt;
}

p a {
  display: inline-block !important;
  text-decoration: underline;
  text-indent: 0;
}

h1, p, h2, h4 {
  padding: 10px;
}

h2 {
  font-family: Authentic, sans-serif;
  font-size: 20pt;
  line-height: 1em;
}

.btn-wrapper {
  position: fixed;
  width: 75%;
  left: 20%;
  top: 0;
  background-color: transparent;
  z-index: 8934239;
  text-align: right;
}

.change-behind {
  font-size: 50px;
  padding: 10px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}

/* thumbnails, keep caption and image on same page */
.thumb {
  break-inside: avoid;
  margin-top: 1%;
  margin-bottom: 1%;
}

.thumbinner {
  margin: 0 auto;
  width: auto !important;
}

img.thumbimage {
  width: 100%;
  height: auto;
}

.layout-1 img,
.layout-2 img {
  border-radius: 0;
  max-width: 100%;
  max-height: 100%;
  margin: 0;
}

.thumbinner .thumbcaption {
  font-size: 9px !important;
}

blockquote p {
  text-indent: 0 !important;
  margin-left: 50px !important;
  line-height: 1.5;
}

.layout-1 p {
  text-indent: 0;
}

/* mediawiki gives thumbs an inline fixed width */
.toc-wrap {
  width: 20%;
  height: 100%;
  background-color: lightgray;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  overflow: scroll;
}

.toclimit-3 ul {
  padding-left: 10px;
  margin-left: 12px;
  border-left: solid 0.5px;
  list-style: none !important;
}

li.tocsection-1,
li.toclevel-1 {
  margin-bottom: 15px;
  margin-right: 5px;
}

.toctext {
  hyphens: auto;
}

li.toclevel-1,
li.toclevel-2 {
  list-style: none !important;
}

li.toclevel-3,
li.toclevel-4 {
  display: none;
}

a:hover {
