/*
Setting named-pages
https://pagedjs.org/documentation/8-named-page/
*/
@page empty-page {
  background-color: blue !important;
}
.title-page {
  page: chapter-title;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

a {
  text-decoration: none;
  color: black;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

/* Generic css */
/* Show url of a tags */
/* a::after {
content: " (" attr(href) ")";
} */
a.image {
  display: block;
  text-align: center;
}

.pagedjs_pages .pagedjs_area .chapter .image-container {
  margin: 0;
  padding: 0;
  text-indent: 0;
  text-align: center;
}
.pagedjs_pages .pagedjs_area .chapter .image-container a {
  display: block;
}

@page chapter-title {
  @top-center {
    content: "";
  }
  @bottom-center {
    content: "";
  }
  @top-left {
    content: "";
  }
  @top-right {
    content: "";
  }
}
/* 
	Change the display of these margin elements to inline-block
	so they have a meaningful width for use with the P5js sketch.
*/
.pagedjs_pages .pagedjs_margin-top-center .pagedjs_margin-content,
.pagedjs_pages .pagedjs_margin-bottom-center .pagedjs_margin-content {
  display: inline-block;
  width: auto !important;
  margin: 0 auto;
}

/* For placement op p5js sketch relative to the page */
.pagedjs_page,
.pagedjs_pagebox {
  position: relative;
}